/* global SwaggerUIBundle, SwaggerUIStandalonePreset, axios */

const app = {
  el: '#app',
  data: {
    host: 'livejs-api.hexschool.io',
  },
  methods: {
    async getData() {
      try {
        const res = await axios.get('./data/swagger_output.json');

        res.data.host = app.data.host;
        this.swaggerFn(res.data);
        this.hideModels();
      } catch (e) { /* empty */ }
    },
    hideModels() {
      const models = document.querySelector('html body div#app section.swagger-ui.swagger-container div.swagger-ui div div.wrapper section.block.col-12.block-desktop.col-12-desktop section.models.is-open');
      models.remove();
    },
    swaggerFn(data) {
      window.ui = SwaggerUIBundle({
        spec: data,
        dom_id: app.el,
        deepLinking: true,
        presets: [
          SwaggerUIBundle.presets.apis,
          SwaggerUIStandalonePreset,
        ],
        plugins: [
          SwaggerUIBundle.plugins.DownloadUrl,
        ],
        layout: 'StandaloneLayout',
      });
    },
  },
  created() {
    this.methods.getData();
  },
};

window.onload = () => {
  app.created();
};
